.sub-nav {
  &-wrap {
    display: flex;
    padding-left: 15px;
    box-shadow: 0px 1px 0px 0px rgba(255, 255, 255, 0.1);
  }
  &-item {
    margin-right: 18px;
    height: 36px;
    font-size: 12px;
    font-weight: 600;
    color: rgba(255, 255, 255, 0.5);
    line-height: 36px;
    position: relative;
    &:link,
    &:visited {
      color: rgba(255, 255, 255, 0.5);
    }
    &:last-child {
      margin-right: 0;
    }
    &-active {
      color: #fff;
      &:link,
      &:visited {
        color: #fff;
      }
      &:after {
        content: '';
        width: 23px;
        height: 4px;
        background: linear-gradient(135deg, #9017ff 0%, #5a14e1 100%);
        border-radius: 2px;
        left: 50%;
        transform: translateX(-50%);
        bottom: 0;
        position: absolute;
      }
    }
  }
  &-item-icon {
    width: 13px;
    margin-right: 4px;
  }
  &-item-corner {
    width: 20px;
    position: absolute;
    right: 0;
    top: 0;
    transform: translate(100%, 0);
  }
  &-back {
    padding: 0 15px;
    height: 46px;
    width: 100%;
    display: flex;
    align-items: center;
    &-icon {
      width: 12px;
      height: 12px;
      cursor: pointer;
    }
  }
}
