@import './var.scss';
$blue: #5915e1;
$hoverBlue: #3940b7;
$purple: rgba(72, 80, 229, 0.2);
$hoverPurple: #bdc0f5;
$bgColor: #140736;

.pair-detail-page,
.token-detail-page {
  .mobile {
    display: none;
  }
  .detail-trad-wrap {
    padding: 20px;
    margin-top: 30px;
    border-radius: 11px;
    background: #2f2f41;
    .single-pair {
      .pr-title {
        display: flex;
        align-items: center;
        img {
          display: block;
          width: 50px;
          height: 50px;
          &:nth-child(2) {
            margin-left: -10px;
          }
        }
        .left10 {
          flex: 1;
          margin-left: 10px;
          font-size: 30px;
          font-family: PingFangSC-Semibold, PingFang SC;
          font-weight: 600;
          color: #ffffff;
          line-height: 42px;
        }
      }
      .pr-bottom {
        display: flex;
        align-items: center;
        margin-top: 30px;
        .price-item {
          display: flex;
          align-items: center;
          margin-right: 50px;
          &:last-child {
            margin-right: 0;
          }
          img {
            display: block;
            margin-right: 10px;
            width: 20px;
            height: 20px;
          }
          .token-name {
            font-size: 20px;
            font-family: PingFangSC-Regular, PingFang SC;
            font-weight: 400;
            color: #ffffff;
            line-height: 28px;
            &:hover {
              color: #c1a0ff;
            }
          }
        }
      }
      .view-button {
        padding: 11px 22px;
        font-size: 20px;
        font-family: PingFangSC-Semibold, PingFang SC;
        font-weight: 600;
        color: #ffffff;
        line-height: 28px;
        border-radius: 10px;
        margin-left: 20px;
        cursor: pointer;
      }
      .add-liq {
        background: #decdff;
        color: #5a14e1;
        &:hover {
          background: #bdc0f5;
        }
      }
      .trade {
        background: #6210ff;
        &:hover {
          background-color: #4711b4;
        }
      }
    }
  }
  .detail-card-wrap {
    margin: 30px auto;
    .detail-card-inner {
      display: flex;
      padding: 20px 0;
      background: #2f2f41;
      border-radius: 11px;
      .detail-card-item {
        flex: 1;
        border-right: 1px solid rgba(216, 216, 216, 0.1);
        &:last-child {
          border-right: 0;
        }
      }
    }

    .stats-card {
      padding: 0 30px;
      .title {
        font-size: 20px;
        font-family: PingFangSC-Semibold, PingFang SC;
        font-weight: 600;
        color: rgba(255, 255, 255, 0.5);
        line-height: 28px;
      }
      .stats-data {
        display: flex;
        margin-top: 20px;
        color: #fff;
        align-items: flex-end;
      }
      .card-num {
        font-family: PingFangSC-Semibold, PingFang SC;
        font-size: 26px;
        line-height: 37px;
        margin-right: 10px;
        font-weight: 600;
      }
      .card-trend {
        position: relative;
        flex: 1;
        top: -5px;
        font-size: 15px;
        white-space: nowrap;
      }
    }
    .pool-token-card {
      margin-top: 9px;
      .flex-row {
        display: flex;
        align-items: center;
        margin-bottom: 8px;
        &:last-child {
          margin-bottom: 0;
        }
        img {
          display: block;
          margin-right: 10px;
          width: 20px;
          height: 20px;
        }
        span {
          font-size: 15px;
          font-family: PingFangSC-Semibold, PingFang SC;
          font-weight: 600;
          color: #ffffff;
          line-height: 21px;
        }
      }
    }
  }
  .detail-chart-wrap {
    margin-top: 30px;
    margin-bottom: 60px;
  }
  .pair-info-wrap {
    margin-top: 30px;
    .table-wrap {
      border-radius: 20px;
      overflow: hidden;
    }
  }
}

.token-detail-page {
  .token-header-wrap {
    margin-top: 24px;
    .token-header-inner {
      display: flex;
      align-items: center;
      padding: 30px 0;
      color: #fff;
      .token-header-main {
        display: flex;
        flex: 1;
        .token-logo {
          margin-right: 20px;
          width: 70px !important;
          height: 70px !important;
          border-radius: 100%;
          border: 1px solid #fff;
        }
        .token-label {
          flex: 1;
          font-family: PingFangSC-Semibold, PingFang SC;

          .token-symbol {
            font-size: 30px;
            font-weight: 600;
            line-height: 42px;
          }
          .sub {
            display: flex;
            align-items: center;
            margin-top: 7px;
          }
          .token-rate {
            margin-right: 10px;
            font-size: 20px;
            font-weight: 600;
            line-height: 28px;
          }
          .color {
            font-size: 15px;
            font-weight: 600;
            line-height: 21px;
          }
        }
      }
      .token-btn-list {
        display: flex;
        .token-btn {
          margin-right: 20px;
          padding: 11px 22px;
          line-height: 28px;
          font-size: 20px;
          border-radius: 10px;
          font-weight: 600;
          cursor: pointer;
          user-select: none;

          &:last-child {
            margin-right: 0;
          }
        }
        .add-liq {
          background: #decdff;
          color: #5a14e1;
          &:hover {
            background: #bdc0f5;
          }
        }
        .trade {
          background: rgba(98, 16, 255, 1);
          &:hover {
            background-color: #4711b4;
          }
        }
      }
    }
  }
  .detail-card-wrap {
    margin-top: 0;
    .stats-card {
      // padding-left: 70px;
      padding-right: 10px;
    }
  }
  .token-pair-wrap {
    margin: 60px 0;
  }
}

#root .scan-detail-table {
  &.mobile {
    display: none;
  }
  overflow: hidden;
  background: #2f2f41;

  .ant-table {
    border-radius: 0;
    color: #fff;
    td,
    th {
      flex: 1;
    }
    .ant-table-measure-row {
      display: none;
    }
    .ant-table-container,
    .ant-table-content {
      border-radius: 0;
    }
  }

  .ant-table table thead {
    border: 0 !important;
    & > tr > th {
      padding: 12px 0 !important;
      color: #fff;
      background: #39394a !important;
      border-radius: 0 !important;
      text-align: left;
      color: rgba(255, 255, 255, 0.5);
      &:first-child {
        flex: 0 1 auto;
        padding-left: 20px !important;
        // text-indent: 27px;
        background: #39394a !important;
      }
    }
  }

  .ant-table .ant-table-tbody {
    tr > td {
      &:first-child {
        flex: 0 1 auto;
        padding-left: 40px !important;
        background: #2f2f41;
      }
    }
    .ant-table-cell-fix-left {
      background: transparent;
    }

    tr td {
      color: #fff !important;
      padding: 20px 5px !important;
      border-bottom: 1px solid rgba(255, 255, 255, 0.1) !important;
    }

    & > tr:hover > td {
      background: #363549 !important;
    }

    tr:last-child {
      td {
        border-bottom: 0 !important;
      }
    }
    .ant-table-placeholder {
      td {
        &:first-child {
          flex: 1 !important;
          text-align: center !important;
          &:first-child {
            padding: 20px 5px !important;
          }
        }
      }
    }
  }

  .ant-table-column-sorters {
    padding: 0;
  }

  .ant-table-thead th.ant-table-column-sort {
    background: transparent;
  }

  td.ant-table-column-sort {
    background: transparent;
  }

  .ant-table-column-sorter-up.active,
  .ant-table-column-sorter-down.active {
    color: $blue;
  }

  .ant-table-pagination.ant-pagination {
    margin-top: 0;
    margin-bottom: 0;
    border-top: none;
    padding: 30px;
  }

  .ant-table-pagination.ant-pagination {
    & > .ant-pagination-item {
      margin-left: 8px;
    }

    .ant-pagination-item-1 {
      margin-left: 0;
    }
  }

  .ant-pagination.mini .ant-pagination-item {
    border-radius: 5px;
    border: 1px solid rgba(255, 255, 255, 0.3);
    color: rgba(255, 255, 255, 0.3);
    overflow: hidden;
  }

  .ant-pagination-item a {
    color: rgba(255, 255, 255, 0.3);
  }

  .ant-pagination-item-active {
    background: #6d00f4;

    border-color: rgba(109, 0, 244, 1);
    a {
      color: rgba(255, 255, 255, 1);
    }
  }

  .ant-pagination-jump-next,
  .ant-pagination-jump-prev {
    min-width: 24px;
    margin-left: 8px;
    border-radius: 5px;
    border: 1px solid rgba(255, 255, 255, 0.3);
    color: rgba(255, 255, 255, 0.3);
    .ant-pagination-item-link {
      .ant-pagination-item-ellipsis {
        font-size: 12px;
        // line-height: 24px;
        color: rgba(255, 255, 255, 0.5);
      }
      .ant-pagination-item-container {
        .ant-pagination-item-link-icon {
          margin-left: 0;
          color: rgba(255, 255, 255, 0.5);
        }
      }
    }
  }

  .action-title {
    white-space: nowrap;
  }

  .ant-pagination .ant-pagination-disabled {
    opacity: 0.25;
  }
}

#root .transaction-table {
  border-radius: 20px;
  .action-title {
    & > span {
      position: relative;
      display: inline-block;
      margin-left: 13px;
      cursor: pointer;
      color: rgba(255, 255, 255, 0.5);
      transition: all 0.2s;
      border-bottom: 2px solid transparent;
    }

    .active-type {
      color: rgba(255, 255, 255, 1);
      border-bottom: 2px solid #d8d8d8;
      // &:after {
      //   content: ' ';
      //   position: absolute;
      //   left: 50%;
      //   bottom: -2px;
      //   width: 50%;
      //   height: 2px;
      //   background: #d8d8d8;
      //   transform: translateX(-50%);
      // }
    }

    & > span:first-child {
      margin-left: 0;
    }
  }
  .typo-text-link {
    color: #c1a0ff;
  }
}

#root .pair-info-table {
  .pointer {
    display: inline-block;
    margin-left: 6px;
    cursor: pointer;
    img {
      position: relative;
      top: -1px;
      width: 14px;
      height: 14px;
    }
  }
  .ant-table table thead {
    & > tr > th {
      &:first-child {
        padding-left: 30px !important;
      }
    }
  }

  .ant-table .ant-table-tbody {
    tr > td {
      &:first-child {
        padding-left: 30px !important;
      }
      &:last-child {
        padding-right: 30px !important;
        min-width: 180px;
        text-align: right;
      }
    }
  }

  .view-button {
    background: #6210ff;
    border-radius: 4px;
    padding: 5px 20px;
    transition: all 0.2s;
    &:hover {
      background: #4711b4;
    }
    a {
      font-size: 15px;
      font-family: PingFangSC-Semibold, PingFang SC;
      font-weight: 600;
      color: #ffffff;
      line-height: 21px;
    }
  }
}

@include mobile-only {
  .pc {
    display: none;
  }
  .mobile {
    display: block;
  }
  .pair-detail-page,
  .token-detail-page {
    .detail-trad-wrap {
      margin-top: 15px;
      padding: 15px 10px;
      border-radius: 6px;
      background: #2f2f41;
      .single-pair {
        .pr-title {
          img {
            display: block;
            width: 25px;
            height: 25px;
            &:nth-child(2) {
              margin-left: -5px;
            }
          }
          .left10 {
            margin-left: 5px;
            font-size: 15px;
            line-height: 21px;
          }
        }
        .pr-bottom {
          margin-top: 15px;
          display: block;
          .price-item {
            margin-right: 0;
            margin-bottom: 5px;
            &:last-child {
              margin-bottom: 0;
            }
            img {
              margin-right: 5px;
              width: 14px;
              height: 14px;
            }
            .token-name {
              font-size: 12px;
              line-height: 14px;
            }
          }
        }
        .view-button {
          padding: 5px 11px;
          font-size: 12px;
          line-height: 14px;
          border-radius: 6px;
          &:first-child {
            margin-left: 0;
          }
        }
        .btn-wrap {
          display: flex !important;
          margin-top: 15px;
        }
      }
    }
    .detail-card-wrap {
      margin: 15px auto;
      .detail-card-inner {
        display: block;
        padding: 0 10px;
        border-radius: 6px;
        .detail-card-item {
          flex: 1;
          border-right: 0;
          border-bottom: 1px solid rgba(216, 216, 216, 0.1);
          &:last-child {
            border-bottom: 0;
          }
        }
      }

      .stats-card {
        padding: 10px 0;
        .title {
          font-size: 18px;
          font-family: PingFangSC-Semibold, PingFang SC;
          font-weight: 600;
          color: rgba(255, 255, 255, 0.5);
          line-height: 24px;
        }
        .stats-data {
          margin-top: 10px;
        }
        .card-num {
          font-size: 15px;
          line-height: 21px;
          margin-right: 5px;
        }
        .card-trend {
          top: -1px;
          font-size: 12px;
        }
      }
      .pool-token-card {
        margin-top: 9px;
        .flex-row {
          display: flex;
          align-items: center;
          margin-bottom: 8px;
          &:last-child {
            margin-bottom: 0;
          }
          img {
            display: block;
            margin-right: 10px;
            width: 20px;
            height: 20px;
          }
          span {
            font-size: 15px;
            font-family: PingFangSC-Semibold, PingFang SC;
            font-weight: 600;
            color: #ffffff;
            line-height: 21px;
          }
        }
      }
    }
    .detail-chart-wrap {
      margin-top: 15px;
      margin-bottom: 30px;
    }
    .pair-info-wrap {
      margin-top: 15px;
      .table-wrap {
        border-radius: 6px;
      }
    }
  }

  #root .scan-detail-table {
    &.pc {
      display: block;
    }
    &.mobile {
      display: none;
    }

    .ant-table .ant-table-thead > tr > th {
      padding: 10px 0 !important;
      &:first-child {
        flex: 0 1 auto;
        padding-left: 10px !important;
      }
    }

    .ant-table .ant-table-tbody {
      tr > td {
        &:first-child {
          flex: 0 1 auto;
          padding-left: 10px !important;
        }
      }
      tr td {
        padding: 10px 2px !important;
      }
    }

    .ant-table-pagination.ant-pagination {
      padding: 20px;
    }
  }

  #root .transaction-table {
    border-radius: 6px;
    .ant-table .ant-table-thead > tr > th {
      &:first-child {
        width: 210px;
      }
    }
    .ant-table .ant-table-tbody {
      tr > td {
        &:first-child {
          width: 210px;
        }
      }
    }
  }
  #root .pair-info-table {
    .ant-table .ant-table-thead > tr > th {
      &:first-child {
        width: 150px;
      }
    }
    .ant-table .ant-table-tbody {
      tr > td {
        &:first-child {
          width: 150px;
        }
        &:last-child {
          min-width: 170px;
          padding-right: 10px !important;
        }
      }
    }

    .view-button {
      border-radius: 4px;
      padding: 2px 5px;
      a {
        font-size: 14px;
        line-height: 21px;
      }
    }
  }
  #root .token-detail-page {
    .token-header-wrap {
      margin-top: 5px;
      .token-header-inner {
        display: block;
        padding: 15px 0;
        .token-header-main {
          .token-logo {
            margin-right: 10px;
            width: 35px !important;
            height: 35px !important;
          }
          .token-label {
            .token-symbol {
              font-size: 15px;
              line-height: 21px;
            }
            .sub {
              margin-top: 3px;
            }
            .token-rate {
              margin-right: 5px;
              font-size: 12px;
              line-height: 14px;
            }
            .color {
              font-size: 12px;
              line-height: 14px;
            }
          }
        }
        .token-btn-list {
          margin-top: 10px;
          .token-btn {
            margin-right: 10px;
            padding: 5px 10px;
            line-height: 14px;
            font-size: 12px;
            border-radius: 4px;

            &:last-child {
              margin-right: 0;
            }
          }
        }
      }
    }
    .pair-info-table {
      .ant-table .ant-table-thead > tr > th {
        &:first-child {
          width: 120px;
        }
      }
      .ant-table .ant-table-tbody {
        tr > td {
          &:first-child {
            width: 120px;
          }
        }
      }
    }
    .token-pair-wrap {
      margin: 30px 0;
    }
  }
}
