:root {
  --header-height: 134px;
}
.main-header-wrap {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  z-index: 100;
  transition: transform ease 500ms;
  &-scroll, &-mobile {
    background: #141525;
  }
  &-hide {
    transform: translateY(-100%);
  }
}

.bottom {
  &-nav {
    &-wrap {
      position: fixed;
      left: 0;
      right: 0;
      bottom: 0;
      color: #fff;
      z-index: 101;
      font-size: 12px;
      font-weight: 600;
      background: #141525;
      box-shadow: 0px -1px 0px 0px rgba(255,255,255,0.1);
    }
    &-list {
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      height: 48px;
    }
    &-item {
      flex: 1;
      text-align: center;
      cursor: pointer;
      color: rgba(255, 255, 255, .5);
      &:link, &:visited, &:hover, &:active {
        color: rgba(255, 255, 255, .5);
      }
      &-text {
        line-height: 1;
        font-size: 14px;
        transform: scale(0.86);
      }
      &-active {
        color: #fff;
        &:link, &:visited, &:hover, &:active {
          color: #fff;
        }
      }
      &-with-sub {
        position: relative;
      }
      &-sub-open {
        width: 55px;
        height: 48px;
        background: rgba(255, 255, 255, .1);
        border-radius: 10px;
      }
    }
    &-icon {
      width: 31px;
      height: 31px;
    }
    &-mask {
      position: fixed;
      top: 0;
      bottom: 48px;
      left: 0;
      right: 0;
      background: rgba(0, 0, 0, .5);
    }
  }
  &-sub-nav {
    position: absolute;
    bottom: 48px;
    left: 50%;
    transform: translateX(-50%);
    z-index: 10;
    width: 153px;

    &-arrow {
      width: 100%;
      height: 14px;
      background-image: url("../../assets/images/menus/nav_arrow.svg");
      background-position: bottom center;
      background-repeat: no-repeat;
    }
    &-inner {
      background: #444454;
      border-radius: 10px;
      overflow: hidden;
    }
    &-item {
      display: block;
      width: 100%;
      height: 40px;
      font-weight: 600;
      line-height: 40px;
      border-radius: 4px;
      font-size: 14px;
      cursor: pointer;
      color: #fff;
      position: relative;
      &-icon {
        width: 13px;
        height: 14px;
        margin-right: 6px;
      }
      &-corner {
        width: 20px;
        height: 8px;
        position: absolute;
        right: 22px;
        top: 6px;
      }
      &:link, &:visited, &:hover {
        color: #fff;
      }
      &:active {
        background: #2F2F41;
      }
      &-active {
        background: #5A14E1;
      }
    }
  }
}
@supports (bottom: constant(safe-area-inset-bottom)) {
  .bottom-nav {
    &-wrap {
      padding-bottom: constant(safe-area-inset-bottom);
    }
    &-mask {
      bottom: calc(48px + constant(safe-area-inset-bottom));
    }
  }
  .main {
    margin-bottom: calc(48px + constant(safe-area-inset-bottom)) !important;
  }
  #root .footer {
    bottom: calc(48px + constant(safe-area-inset-bottom));
  }
}

@supports (bottom: env(safe-area-inset-bottom)) {
  .bottom-nav {
    &-wrap {
      padding-bottom: env(safe-area-inset-bottom);
    }
    &-mask {
      bottom: calc(48px + env(safe-area-inset-bottom));
    }
  }
  .main {
    margin-bottom: calc(48px + env(safe-area-inset-bottom)) !important;
  }
  #root .footer {
    bottom: calc(48px + env(safe-area-inset-bottom));
  }
}