@import './var.scss';

#root .base-scan-search {
  flex: 1;
  font-size: 15px;
  color: rgb(255, 255, 255, 0.7);

  .ant-select {
    background: rgba(47, 47, 65, 1);
    border-radius: 10px;
    .ant-select-selector {
      width: 100%;
      border-radius: 10px;
      background: transparent;
    }
  }
  .ant-input-affix-wrapper {
    padding: 0 20px;
    width: 100%;
    background: transparent;
    border: 0;
    & > input.ant-input {
      background: transparent;
      border: 0;
      color: #fff;
      &::placeholder {
        color: rgba(255, 255, 255, 0.5);
      }
    }
  }
  .ant-input-disabled {
    background: transparent !important;
  }

  .search-more {
    padding: 10px 20px;
    font-size: 12px;
    display: inline-block;
    color: #434be0;
    cursor: pointer;
  }

  .ant-select-item {
    padding: 0;

    &:first-child {
      &:hover {
        background: #fff;
      }
    }
  }

  .ant-input-prefix {
    color: #fff;
    margin-right: 10px;

    img {
      width: 16px;
      height: 16px;
    }
  }
}

.certain-category-search-loading {
  // background: rgb(255, 255, 255, 0.7);
  // cursor: not-allowed;
  // .ant-select-item {
  //   cursor: not-allowed;
  // }
  // .ant-select-item-option-active {
  //   background: #fff !important;
  // }
}
.certain-category-search-dropdown {
  &.ant-select-dropdown {
    background: #fff !important;
    .ant-select-item {
      background: #fff !important;
      &:hover {
        background: rgba(89, 21, 225, 0.15) !important;
      }
    }
    .ant-select-item-group {
      background: #fff !important;
      color: #333333;
      &:hover {
        background: #fff !important;
        color: #333333 !important;
      }
    }
  }

  .dropdown-item-token {
    padding-left: 10px;

    img {
      width: 20px;
      height: 20px;
      border-radius: 50%;
    }

    .trxLogo {
      margin-left: -5px;
    }

    .name {
      font-family: Avenir Next;
      font-style: normal;
      font-weight: 500;
      font-size: 14px;
      line-height: 20px;
      color: #333333;
      margin-left: 14px;
      padding: 11px 0;
      display: inline-block;
    }
  }

  .dropdown-item-title {
    display: block;
    padding: 10px;
    color: #333333;
  }

  .ant-select-item-group .dropdown-item-title {
    padding: 5px 10px;
  }

  .ant-select-item-option-grouped {
    padding-left: 11px;
  }

  .search-more {
    padding: 10px;
    font-size: 12px;
    display: inline-block;
    color: #434be0;
    cursor: pointer;
  }
}

@include mobile-only {
  #root .base-scan-search {
    margin-left: 12px;
    .ant-select {
      border-radius: 26px;
      .ant-select-selector {
        height: 26px;
        border-radius: 26px;
      }
    }

    .ant-input-affix-wrapper {
      padding: 0 15px;
      & > input.ant-input {
        height: 26px;
        font-size: 12px;
        border-radius: 26px;
      }
    }
    .ant-input-prefix {
      margin-right: 11px;
      img {
        width: 12px;
        height: 12px;
      }
    }
  }

  .certain-category-search-dropdown {
    min-width: 100% !important;
    width: 100% !important;
    left: 0 !important;
    // top: 50px !important;
    .dropdown-item-token {
      padding-left: 0;

      .name {
        margin-left: 9px;
        padding: 9px 0;
      }
    }

    .dropdown-item-title {
      display: block;
      padding: 10 0px;
    }

    .ant-select-item-group .dropdown-item-title {
      padding: 5px 0;
    }

    .ant-select-item-option-grouped {
      padding-left: 11px;
    }

    .search-more {
      padding: 5px 0;
    }
  }
}
